import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

import Paper from './Paper';
import { useTranslation } from '../providers/TranslationProvider';

const IMAGE_WIDTH = 138;

const useProductTagStyles = makeStyles((theme) => ({
  tag: {
    position: 'absolute',
    padding: theme.spacing(0.5),
    backgroundColor: theme.palette.secondary.main,
    color: '#FFFFFF',
    right: 0,
    bottom: (props) => theme.spacing(2),
  },
}));

function ProductTag(props) {
  const { product } = props;

  const classes = useProductTagStyles();
  const { t } = useTranslation();

  if (product.unavailable) {
    return <div className={classes.tag}>{t('Unavailable')}</div>;
  }

  if (product._displayDiscountPercentage) {
    return (
      <div className={classes.tag}>{product._displayDiscountPercentage}</div>
    );
  }

  return null;
}

const useProductInformation = makeStyles((theme) => ({
  container: {
    width: (props) =>
      props.hasImage ? `calc(100% - ${IMAGE_WIDTH}px)` : '100%',
    padding: theme.spacing(2),
    overflow: 'hidden',
    boxSizing: 'border-box',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  productName: {
    fontWeight: 'bold',
  },
  productDescription: {
    display: '-webkit-box',
    '-webkit-box-orient': 'vertical',
    '-webkit-line-clamp': 2,
    overflow: 'hidden',
  },
  productPrice: {
    color: theme.palette.secondary.main,
    marginTop: theme.spacing(),
  },
  productDiscountedDisplayPrice: {
    textDecoration: 'line-through',
    color: 'black',
  },
}));

function ProductInformation(props) {
  const { product = {} } = props;

  const hasImage = product && product.images && product.images[0];

  const classes = useProductInformation({ hasImage });

  const productPrice = product._displayDiscountPrice ? (
    <>
      <span className={classes.productDiscountedDisplayPrice}>
        {product._displayPrice}
      </span>
      &nbsp;&nbsp;
      {product._displayDiscountPrice}
    </>
  ) : (
    product._displayPrice
  );

  return (
    <div className={classes.container}>
      <Typography
        variant="h5"
        className={classes.productName}
        gutterBottom
        noWrap
      >
        {product.name}
      </Typography>
      <Typography
        variant="body2"
        className={classes.productDescription}
        gutterBottom
      >
        {product.description}
      </Typography>

      <Typography variant="body2" className={classes.productPrice}>
        {productPrice}
      </Typography>
    </div>
  );
}

const useProductImageStyles = makeStyles(() => ({
  productImage: ({ image }) => ({
    position: 'relative',
    display: 'flex',
    justifyContent: 'center',
    width: IMAGE_WIDTH,
    overflow: 'hidden',
    flexShrink: 0,
    backgroundPosition: 'center',
    backgroundImage: `url(${image})`,
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'contain',
  }),
}));

function ProductImage(props) {
  const { product } = props;
  const image = product && product.images && product.images[0];
  const classes = useProductImageStyles({ image });

  return image ? <div className={classes.productImage} /> : null;
}

const useStyles = makeStyles(() => ({
  paper: {
    height: 138,
    width: '100%',
    display: 'flex',
    cursor: 'pointer',
    boxSizing: 'border-box',
  },
}));

function ProductCard(props) {
  const { product = {}, onClick } = props;

  const classes = useStyles();

  const handleClick = React.useCallback(
    (e) => {
      onClick && onClick(e, product.id);
    },
    [onClick, product]
  );

  return (
    <Paper className={classes.paper} onClick={handleClick}>
      <ProductInformation product={product} />
      <ProductImage product={product} />
      <ProductTag product={product} />
    </Paper>
  );
}

export default ProductCard;
