import React, { useState, useCallback, useEffect } from 'react';
import clsx from 'clsx';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';

import EshopAPI from '../EshopAPI';
import Paper from '../components/Paper';
import Carousel from '../components/Carousel';
import QuantityInput from '../components/QuantityInput';
import { useEshop } from '../providers/EshopProvider';
import { useTranslation } from '../providers/TranslationProvider';

const useProductInformationStyles = makeStyles((theme) => ({
  tag: {
    height: 60,
    borderRadius: 4,
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(2),
    backgroundColor: theme.palette.secondary.main,
    marginBottom: theme.spacing(2),
  },
  tagLabel: {
    color: '#FFFFFF',
    fontWeight: 'bold',
  },
  paper: {
    padding: theme.spacing(2),
  },
  productTitle: {
    fontWeight: 'bold',
  },
  productCategoryChip: {
    backgroundColor: theme.palette.secondary.light,
    borderRadius: 4,
    color: theme.palette.secondary.main,
    padding: theme.spacing(),
  },
  productOriginalPrice: {
    textDecoration: 'line-through',
    color: '#000000',
  },
  productPrice: {
    color: theme.palette.secondary.main,
  },
}));

function ProductInformation(props) {
  const { product, quantity, onQuantityChange, onAddProduct } = props;

  const { t } = useTranslation();
  const classes = useProductInformationStyles();

  const isUnavailable = product.unavailable;
  const discountPercentage = product._displayDiscountPercentage;
  const discountedPrice = product._displayDiscountPrice;

  const tag = isUnavailable
    ? t('Unavailable')
    : discountPercentage
    ? `${discountPercentage} ${t('Discount')}`
    : '';

  const categories =
    product._categories && product._categories.length
      ? product._categories.map((category) => (
          <span className={classes.productCategoryChip} key={category.id}>
            {category.name}
          </span>
        ))
      : null;

  const price = (
    <Typography variant="h6" className={classes.productPrice}>
      <span
        className={clsx({ [classes.productOriginalPrice]: discountedPrice })}
      >
        {product._displayPrice}
      </span>
      &nbsp;
      {discountedPrice}
    </Typography>
  );

  return (
    <>
      {tag && (
        <div className={classes.tag}>
          <Typography className={classes.tagLabel} variant="h5">
            {tag}
          </Typography>
        </div>
      )}
      <Paper className={classes.paper}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Typography className={classes.productTitle} variant="h4" noWrap>
              {product.name}
            </Typography>
          </Grid>
          <Grid item xs={12}>
            <Typography>{product.description}</Typography>
          </Grid>
          <Grid item xs={12}>
            {categories}
          </Grid>
          <Grid item xs={12}>
            {price}
          </Grid>
          <Grid item xs={12}>
            <Grid container spacing={1} alignItems="center">
              <Grid item xs={12} md={6}>
                <QuantityInput
                  name="quantity"
                  label={t('Quantity')}
                  value={quantity}
                  disabled={isUnavailable}
                  moreLabel={t('More')}
                  onChange={onQuantityChange}
                />
              </Grid>
              <Grid item xs={12} md={6} align="center">
                <Button
                  disabled={isUnavailable}
                  onClick={onAddProduct}
                  startIcon={<AddShoppingCartIcon />}
                >
                  {t('Add to cart')}
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
}

ProductInformation.defaultProps = {
  product: {},
  quantity: 1,
  onQuantityChange: () => {},
  onAddProduct: () => {},
};

function ProductView(props) {
  const { match = {}, history } = props;
  const { params = {} } = match;
  const { productId } = params;

  const { cart, setItem } = useEshop();

  const [quantity, setQuantity] = useState(1);
  const [product, setProduct] = useState(null);

  const handleAddProduct = useCallback(() => {
    const qty = cart[product.id] || 0;
    setItem(product.id, qty + Number(quantity));
  }, [quantity, product, cart, setItem]);

  const handleQuantityChange = useCallback(
    (e) => setQuantity(Number(e.target.value)),
    []
  );

  useEffect(() => {
    EshopAPI.fetchProduct(productId).then((product) => {
      if (product && product.id) {
        setProduct(product);
      } else {
        history.replace('/eshop');
      }
    });
  }, [history, productId]);

  if (!product) {
    return null;
  }

  return (
    <Grid container spacing={1}>
      <Grid item xs={12} md={3}>
        <Carousel images={product.images} />
      </Grid>
      <Grid item xs={12} md={9}>
        <ProductInformation
          product={product}
          quantity={quantity}
          onAddProduct={handleAddProduct}
          onQuantityChange={handleQuantityChange}
        />
      </Grid>
    </Grid>
  );
}

export default ProductView;
